import api from "@/core/Api";

class FilterRepository {
  /**
   * Create a new Repository instance.
   */
  constructor(requestUrl, filters) {
    this.requestUrl = requestUrl;
    this.filters = {
      page: 1,
      limit: 10,
      ...filters,
    };
    this.filtersActive = false;

    this.response = {};
  }

  /**
   * Get page.
   *
   * @returns {number}
   */
  getPage() {
    return this.filters.page;
  }

  /**
   * Set page.
   *
   * @param {number} page
   */
  setPage(page) {
    this.filters.page = page;

    this.fetch();
  }

  /**
   * Set limit.
   *
   * @returns {number}
   */
  getLimit() {
    return this.filters.limit;
  }

  /**
   * Set limit.
   *
   * @param {number} limit
   */
  setLimit(limit) {
    this.filters.page = 1;
    this.filters.limit = limit;

    this.fetch();
  }

  /**
   * Get sort.
   *
   * @returns {string}
   */
  getSort() {
    return this.filters.sort;
  }

  /**
   * Get sort type.
   *
   * @returns {string}
   */
  getSortType() {
    return this.filters.sort_type;
  }

  /**
   * Set sort fields.
   *
   * @param {string} column
   * @param {"asc", "desc"} type
   */
  setSort(column, type) {
    this.filters.page = 1;
    this.filters.sort = column;
    this.filters.sort_type = type;
    this.fetch();
  }

  /**
   * Get request configs.
   *
   * @returns {object}
   */
  getConfig() {
    let params = {};

    if (this.filtersActive) {
      for (let filter in this.filters) {
        if (this.filters[filter] !== "" && this.filters[filter] !== null) {
          params[filter] = this.filters[filter];
        }
      }
    } else {
      params = {
        page: this.filters.page,
        limit: this.filters.limit,
        sort: this.filters.sort,
        sort_type: this.filters.sort_type,
      };
    }

    return { params: params };
  }

  /**
   * Active filters for request.
   */
  activeFilters() {
    this.filters.page = 1;
    this.filtersActive = true;

    this.fetch();
  }

  /**
   * Disable filters for request.
   */
  disableFilters() {
    this.filters.page = 1;
    this.filtersActive = false;

    this.fetch();
  }

  /**
   * Fetch data from api.
   */
  fetch() {
    api.get(this.requestUrl, this.getConfig()).then((response) => {
      this.response = response.data;
    });
  }

  /**
   * Get fetched items.
   *
   * @returns {object[]}
   */
  getItems() {
    return this.response.data;
  }

  /**
   * Get pagination object.
   * @returns {object}
   */
  getPagination() {
    return this.response;
  }
}

export default FilterRepository;
