<template>
  <div
    v-if="pagination.total > 0"
    key="dataTableHasData"
    class="flex flex-col justify-between px-4 py-4 space-y-4 sm:space-y-0 sm:flex-row sm:items-center sm:px-5"
  >
    <div class="flex items-center space-x-2 text-xs+">
      <span>{{ $t("pagination.limit_count") }}:</span>
      <label class="block">
        <select
          v-model="limit"
          class="rounded-full border border-slate-300 bg-white px-2 py-1 pr-6 table-limit form-select hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:bg-navy-700 dark:hover:border-navy-400 dark:focus:border-accent"
          @change="$emit('limit-changed', limit)"
        >
          <option value="5">5</option>
          <option value="10">10</option>
          <option value="15">15</option>
          <option value="30">30</option>
          <option value="50">50</option>
        </select>
      </label>
    </div>

    <ol v-if="hasPages" key="paginationHasPages" class="pagination">
      <li class="rounded-l-full bg-slate-150 dark:bg-navy-500">
        <button
          :disabled="onFirstPage"
          type="button"
          class="flex h-8 w-8 items-center justify-center rounded-full text-slate-300 transition-colors dark:text-navy-400"
          :class="{
            '!text-slate-500 hover:bg-slate-300 focus:bg-slate-300 active:bg-slate-300/80 dark:!text-navy-200 dark:hover:bg-navy-450 dark:focus:bg-navy-450 dark:active:bg-navy-450/90':
              !onFirstPage,
          }"
          @click="pageChanged(pagination.current_page - 1)"
        >
          <v-icon name="chevron-left" size="4.5"></v-icon>
        </button>
      </li>
      <li
        v-for="n in pagination.last_page"
        :key="n"
        class="bg-slate-150 dark:bg-navy-500"
      >
        <button
          type="button"
          class="flex h-8 items-center justify-center rounded-full px-3 leading-tight transition-colors min-w-[2rem] hover:bg-slate-300 focus:bg-slate-300 active:bg-slate-300/80 dark:hover:bg-navy-450 dark:focus:bg-navy-450 dark:active:bg-navy-450/90"
          :class="{
            'bg-primary text-white hover:!bg-primary-focus focus:!bg-primary-focus active:bg-primary-focus/90 dark:bg-accent dark:hover:bg-accent-focus dark:focus:bg-accent-focus dark:active:bg-accent/90':
              pagination.current_page === n,
          }"
          :aria-current="pagination.current_page === n ? 'page' : 'step'"
          @click="pageChanged(n)"
        >
          {{ n }}
        </button>
      </li>
      <li class="rounded-r-full bg-slate-150 dark:bg-navy-500">
        <button
          :disabled="onLastPage"
          type="button"
          class="flex h-8 w-8 items-center justify-center rounded-full text-slate-300 transition-colors dark:text-navy-400"
          :class="{
            '!text-slate-500 hover:bg-slate-300 focus:bg-slate-300 active:bg-slate-300/80 dark:!text-navy-200 dark:hover:bg-navy-450 dark:focus:bg-navy-450 dark:active:bg-navy-450/90':
              !onLastPage,
          }"
          @click="pageChanged(pagination.current_page + 1)"
        >
          <v-icon name="chevron-right" size="4.5"></v-icon>
        </button>
      </li>
    </ol>

    <div class="text-xs+">
      {{
        $t("pagination.show_count", {
          total: pagination.total,
          first: pagination.from,
          last: pagination.to,
        })
      }}
    </div>
  </div>
  <div v-else class="px-4 py-4 text-center text-lg font-medium">
    {{ $t("pagination.data_empty") }}
  </div>
</template>

<script>
export default {
  name: "DataTablePagination",
  emits: ["page-changed", "limit-changed"],
  props: {
    pagination: {
      type: Object,
      required: false,
    },
  },
  data() {
    return {
      limit: 10,
    };
  },
  methods: {
    pageChanged(page) {
      this.$emit("page-changed", page);
    },
  },
  computed: {
    hasPages() {
      return this.pagination.last_page !== 1;
    },
    onFirstPage() {
      return this.pagination.current_page === 1;
    },
    onLastPage() {
      return this.pagination.current_page === this.pagination.last_page;
    },
  },
};
</script>
