<template>
  <div class="flex items-center justify-between py-5 lg:py-6">
    <div class="flex items-center space-x-4">
      <h2
        class="text-xl font-medium text-slate-800 dark:text-navy-50 lg:text-2xl"
      >
        {{ activeItem.title }}
      </h2>

      <div
        v-if="items.length > 1"
        key="hasBreadcrumbItem"
        class="mx-4 w-px self-stretch bg-slate-300 dark:bg-navy-600"
      ></div>

      <ul
        v-if="items.length > 1"
        key="hasBreadcrumbItem"
        class="hidden flex-wrap items-center space-x-2 sm:flex"
      >
        <breadcrumb-list-item
          v-for="(item, index) in items"
          :key="index"
          :title="item.title"
          :route="item.route"
          :active="item.active"
        ></breadcrumb-list-item>
      </ul>
    </div>

    <div v-if="$slots._" key="breadcrumbSlotExists" class="flex gap-2 buttons">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import BreadcrumbListItem from "@/components/BreadcrumbList/BreadcrumbListItem.vue";

export default {
  name: "BreadcrumbList",
  props: {
    items: Array,
  },
  components: { BreadcrumbListItem },
  computed: {
    activeItem() {
      return this.items.find(function (item) {
        return item.active && item.active === true;
      });
    },
  },
};
</script>
