<template>
  <component :is="iconComponent" role="img" :class="[iconSize]" />
</template>

<script>
import { startCase } from "lodash";

const icons = require.context("./icons", true, /[A-Z]\w+\.(vue|js)$/);

export default {
  name: "VIcon",
  props: {
    name: {
      type: String,
      required: true,
    },
    size: {
      type: [String, Number],
      default: 6,
    },
  },

  computed: {
    componentName() {
      return startCase(this.name).replaceAll(" ", "");
    },
    iconComponent() {
      const iconKey = `./${this.componentName}Icon.vue`;
      if (icons.keys().includes(iconKey)) {
        return require(`./icons/${this.componentName}Icon.vue`).default;
      }

      return null;
    },
    iconSize() {
      return `w-${this.size} h-${this.size}`;
    },
  },
};
</script>
