  created() {
    this.user = this.$store.getters.user;
  },
  methods: {
    logout() {
      this.$store.dispatch("logout");
      this.$router.push({ name: "login" });
    },
  },
  computed: {
    fullName() {
      if (!this.user) return;

      return `${this.user.first_name} ${this.user.surname}`;
    },
  },
};
