<template>
  <teleport to="#modals">
    <transition name="fade">
      <div
        v-if="show"
        key="isModalShow"
        role="dialog"
        class="fixed inset-0 flex flex-col items-center justify-center overflow-hidden px-4 py-6 modal z-[100] sm:px-5"
      >
        <div class="absolute inset-0 bg-slate-900/60 modal-overlay"></div>
        <div
          class="relative w-1/5 max-w-3xl rounded-lg bg-white px-4 py-8 text-center modal-content dark:bg-navy-700 sm:px-6"
        >
          <slot name="icon" class="mb-4"></slot>

          <div>
            <h2 class="text-2xl text-slate-700 dark:text-navy-100">
              <slot name="title"></slot>
            </h2>
            <p class="mt-2">
              <slot name="body"></slot>
            </p>
            <div class="mt-6 flex justify-center space-x-3">
              <v-button color="default" @click="toggle">
                {{ $t("labels.close") }}
              </v-button>

              <slot name="buttons"></slot>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </teleport>
</template>

<script>
export default {
  name: "VModal",
  emits: ["closed"],
  data() {
    return {
      show: false,
    };
  },
  methods: {
    toggle() {
      this.show = !this.show;

      const scrollbarWidth =
        window.innerWidth - document.documentElement.clientWidth;
      if (this.show) {
        document.documentElement.style.paddingRight = `${scrollbarWidth}px`;
        document.documentElement.style.overflow = "hidden";
      } else {
        document.documentElement.style.removeProperty("padding-right");
        document.documentElement.style.removeProperty("overflow");

        this.$emit("closed");
      }
    },
  },
};
</script>

<style scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>
