<template>
  <div class="flex items-center justify-between">
    <slot name="title"></slot>
    <div class="ml-auto flex">
      <button
        @click="toggleFilterPanel()"
        class="h-8 w-8 rounded-full p-0 btn hover:bg-slate-300/20 focus:bg-slate-300/20 active:bg-slate-300/25 dark:hover:bg-navy-300/20 dark:focus:bg-navy-300/20 dark:active:bg-navy-300/25"
      >
        <v-icon name="filter" size="4.5"></v-icon>
      </button>
    </div>
  </div>

  <transition
    name="accordion"
    v-on:before-enter="accordionBeforeEnter"
    v-on:enter="accordionEnter"
    v-on:before-leave="accordionBeforeLeave"
    v-on:leave="accordionLeave"
  >
    <div
      v-if="filterPanel"
      key="isFilterPanelShowAble"
      class="filter-panel"
      :aria-expanded="filterPanel"
    >
      <form @submit.prevent="repository.activeFilters()">
        <slot name="filters"></slot>

        <div class="mt-3 text-end space-x-3">
          <v-button color="default" @click="toggleFilterPanel()">
            {{ $t("labels.close") }}
          </v-button>

          <v-button color="warning" @click="repository.disableFilters()">
            {{ $t("filter.disable") }}
          </v-button>

          <v-button color="primary" @click="repository.activeFilters()">
            {{ $t("filter.apply") }}
          </v-button>

          <slot name="filterButtons"></slot>
        </div>
      </form>
    </div>
  </transition>

  <div class="mt-3 card">
    <div class="min-w-full overflow-x-auto is-scrollbar-hidden">
      <table class="w-full text-left advanced-table is-hoverable">
        <thead>
          <tr>
            <th
              v-for="(item, index) in titles"
              :key="index"
              :class="{
                'rounded-tl-lg': index === 0,
                'rounded-tr-lg': index === titles.length - 1,
                'cursor-pointer': item.sortable && item.sortable === true,
              }"
              @click="changeSort(item)"
            >
              <div class="flex items-center justify-between">
                {{ item.title }}
                <button
                  v-if="item.sortable"
                  key="isSortedColumn"
                  type="button"
                  class="gridjs-sort"
                  :class="{
                    'gridjs-sort-neutral': item.column !== sort,
                    'gridjs-sort-desc':
                      item.column === sort && this.sortType === 'desc',
                    'gridjs-sort-asc':
                      item.column === sort && this.sortType === 'asc',
                  }"
                ></button>
              </div>
            </th>
          </tr>
        </thead>
        <tbody>
          <slot name="body"></slot>
        </tbody>
      </table>
    </div>

    <data-table-pagination
      :pagination="repository.getPagination()"
      @page-changed="(page) => repository.setPage(page)"
      @limit-changed="(page) => repository.setLimit(page)"
    ></data-table-pagination>
  </div>
</template>

<script>
import DataTablePagination from "@/components/DataTable/DataTablePagination.vue";
import FilterRepository from "@/core/FilterRepository";

export default {
  name: "DataTable",
  components: { DataTablePagination },
  props: {
    repository: FilterRepository,
    titles: Array,
  },
  data() {
    return {
      filterPanel: false,
      sort: null,
      sortType: null,
    };
  },
  methods: {
    toggleFilterPanel() {
      this.filterPanel = !this.filterPanel;
    },
    changeSort(item) {
      if (item.sortable) {
        if (this.sort === item.column) {
          this.sortType = this.sortType === "desc" ? "asc" : "desc";
        } else {
          this.sort = item.column;
          this.sortType = "asc";
        }

        this.repository.setSort(this.sort, this.sortType);
      }
    },
    accordionBeforeEnter(el) {
      el.style.height = "0";
    },
    accordionEnter(el) {
      el.style.height = el.scrollHeight + "px";
    },
    accordionBeforeLeave(el) {
      el.style.height = el.scrollHeight + "px";
    },
    accordionLeave(el) {
      el.style.height = "0";
    },
  },
};
</script>

<style>
.accordion-enter-active,
.accordion-leave-active {
  transition: height 400ms;
  overflow: hidden;
}

.accordion-enter-active .vs__search,
.accordion-leave-active .vs__search {
  z-index: -1;
}
</style>
