<template>
  <!-- App preloader -->
  <div
    v-if="isLoading"
    key="isPageLoading"
    class="fixed z-50 grid h-full w-full place-content-center bg-slate-50 app-preloader dark:bg-navy-900"
  >
    <div class="relative inline-block h-48 w-48 app-preloader-inner"></div>
  </div>
</template>

<script>
export default {
  name: "AppPreloader",
  data() {
    return {
      isLoading: false,
    };
  },
  mounted() {
    this.$router.beforeEach((to, from, next) => {
      this.isLoading = true;
      next();
    });

    this.$router.afterEach(() => {
      this.$nextTick(() => {
        this.isLoading = false;
      });
    });
  },
};
</script>
