import { createApp } from "vue";

import router from "./router";
import store from "./store";
import i18n from "./lang/i18n";
import moment from "moment";

// Setting
import { APP_LOCALE } from "@/core/Config";

// Lineone template files
import "./css/app.css";
import "./js/lineone.js";

// Components
import BreadcrumbList from "@/components/BreadcrumbList/BreadcrumbList.vue";
import VueSelect from "vue-select";
import ValidationFeedback from "@/components/ValidationFeedback.vue";
import VModal from "@/components/VModal.vue";
import Toast from "vue-toastification";
import DataTable from "@/components/DataTable/DataTable.vue";
import VIcon from "@/components/VIcon/VIcon.vue";
import VButton from "@/components/VButton.vue";
import VueCleave from "vue-cleave-component";

// App
import AppWrapper from "./AppWrapper.vue";

require("cleave.js/dist/addons/cleave-phone.i18n.js");

document.documentElement.setAttribute("lang", APP_LOCALE);

const app = createApp(AppWrapper);

app.config.globalProperties.$moment = moment;

app.component("vue-select", VueSelect);
app.component("v-modal", VModal);
app.component("breadcrumb-list", BreadcrumbList);
app.component("validation-feedback", ValidationFeedback);
app.component("data-table", DataTable);
app.component("v-icon", VIcon);
app.component("v-button", VButton);
app.component("v-cleave", VueCleave);

app.use(store);
app.use(router);
app.use(i18n);
app.use(Toast);

app.mount("#app");
